import {IValue, IEntity, IMetric, IPeriod, ILocation, IVCPV, IVizelConfig, ISubspace} from "bi-internal/defs/bi";
import axios from 'axios';
import {IDisposable} from "bi-internal/core";
import {data_engine} from "bi-internal/defs/tables";

interface ICircleData {
  value: number,
  strValue: string,
  min: number,
  max: number,
  suffix: string,
}

interface IWave {
  coordinates?: { x: number, y: number }[],
  dx: number,
  fill: string,
  width: number,
  height: number,
  percent?: number,
  className?: string,
  duck?: boolean,
}

interface IRuler {
  width: number,
  height: number,
  ticks: number[]
}

interface IDuck {
  height: number,
  dx: number,
  dy: number,
  duck?: boolean,
  onClick?: any,
}

declare type IRange = [number, number];

const min = (v1: number | null, v2: number | null): number => !Number.isFinite(v1) ? !Number.isFinite(v2) ? null : v2 : !Number.isFinite(v2) ? v1 : Math.min(v1, v2);
const max = (v1: number | null, v2: number | null): number => !Number.isFinite(v1) ? !Number.isFinite(v2) ? null : v2 : !Number.isFinite(v2) ? v1 : Math.max(v1, v2);
const mergeRanges = ([xmin, xmax]: IRange, [ymin, ymax]: IRange): IRange => [min(xmin, ymin), max(xmax, ymax)];

const Ruler = React.memo(({width, height, ticks}: IRuler) => {
  const ruler = height / ticks.length;
  const WIDTHLINE = 17;
  let arrValueRule = [];

  for (let i = 0; i < ticks.length; i++) {
    let d: string;
    if (i % 2 === 0) {
      d = (`M ${width} ${height - i * ruler - ruler} L ${width - WIDTHLINE - 12} ${height - i * ruler - ruler}`);
    } else {
      d = (`M ${width} ${height - i * ruler - ruler} L ${width - WIDTHLINE} ${height - i * ruler - ruler}`);
    }
    arrValueRule.push(<g key={`${ticks[i] / 2}d`}>
      <path key={`${ticks[i] / 3}r`} className='LuxWaves__Ruler__g' d={d}
            stroke={'transparent'} strokeWidth={'15'} fill={'transparent'}>
        <title key={`${ticks[i] / 7}t`}>{ticks[i]}</title></path>
      <path key={`${ticks[i] / 5}l`} className='LuxWaves__Ruler__path' d={d}
            stroke={'rgba(244, 244, 244, 0.4)'} strokeWidth={'1'} fill={'transparent'}/>
    </g>);

  }

  return <>{arrValueRule}</>;
})


const WavePath = React.memo(({coordinates, dx, fill, width, height}: IWave) => {
  if (coordinates) {
    let wavesArr = ['M 0 0'];
    for (let i = 1; i < coordinates.length; i++) {
      let x = coordinates[i].x;
      let nextX = coordinates[i + 1]?.x;
      let y = coordinates[i].y;
      let nextY = coordinates[i + 1]?.y;
      if (nextX && nextY) {
        wavesArr.push(`Q ${x} ${10 * y}, ${x + y * (nextX - x) / (y - nextY)} 0`);
      }
    }
    wavesArr.push(`L ${width * 5 * dx * 3} 0 L ${width * 5 * dx * 3} ${height} L 0 ${height} L 0 0`);

    return (
      <path d={wavesArr.join(' ')}
            stroke={'transparent'} fill={fill} fillRule={"nonzero"}
      />);
  }
  return null;
});


const Wave = React.memo(({className, coordinates, dx, fill, width, height, percent, duck}: IWave) => {

  return (
    <g className={className} style={{transform: `translate(${-width * 4 + 4 * width * percent / 100}px, ${0}px)`}}>
      <WavePath coordinates={coordinates} dx={dx} fill={fill} width={width} height={height}/>
    </g>);
});

